<template>
    <section>
        <tg-list :IsShowAction="true" ModuleName="Ticketing" SubModuleCode="FSM_WorkOrder" :LegendArray="[]" :widgets="[]"
            IdentityColumn="TICKET_ID" :listType="['List']" :HeaderText="$t('Wokr Order')" :ListData="WorkOrderList"
            :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SortExp="SortExp"
            :SortBy="SortBy" :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="workorderActionButtonClick"
            :NorecordfoundText="$t('NoRecordfound')" v-on:PagerButtonClick="pagerMethod">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'status_id'">
                    <span :class="getStatusClassList(data.row.status_id, 'status_name', 'fsm')">
                        {{ data.row.status_id }}
                    </span>
                </template>
            </template>
        </tg-list>

        <WorkOrderStatusModal v-if="showWorkOrderStatusModal" :updateWorkOrderStatus="updateWorkOrderStatus"
            :isLocateorNonLocate="isLocateorNonLocate" :callbackfunction="callbackfunction"></WorkOrderStatusModal>

    </section>
</template>

<script>
import Schema from "@/views/WorkOrder/WorkOrderSchema";
import DataServices from "@/services/DataService";
import WorkOrderStatusModal from "@/components/Modal/WorkOrderStatusModal.vue";
export default {
    components: {
        WorkOrderStatusModal
    },
    props: {
        workorderlist: {
            type: Array,
            required: false
        },
        callbackfunction: {
            type: Function
        },
        CompanyType: {
            type: String,
            required: false
        }
    },
    watch: {
        workorderlist: function (value) {
            debugger;
            this.WorkOrderList = value;
            this.IsListView = this.$parent.IsListView;
        },
    },
    created: async function () {
        debugger;
        var vm = this;
        vm.isLocateorNonLocate = this.CompanyType;
    },
    data() {
        return {
            WorkOrderList: [],
            TotalRecords: 0,
            PageSize: 10,
            PageNumber: 1,
            CurrentPage: 1,
            SortBy: '',
            SortExp: '',
            IsInSlot: true,
            listheaderbuttons: null,
            listActions: Schema.workOrderListActions,
            isLocateorNonLocate: '',
            showWorkOrderStatusModal: false,
            updateWorkOrderStatus: [],
            workOrderModalStatus: false,
            Headers: [{
                "COLUMN_NAME": "workorder_number",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "WO NUMBER",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "status_id",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 2,
                settings: {
                    isInSlot: true,
                }
            },
            {
                "COLUMN_NAME": "worktype_name",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "WO Name",
                "DISPLAY_ORDER ": 3,
            },
            {

                "COLUMN_NAME": "work_order_date",
                "DATA_TYPE": "date",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Work Order Date",
                "DISPLAY_ORDER": 4,

            },
            {
                "COLUMN_NAME": "comment",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Comment",
                "DISPLAY_ORDER": 5,
            },

            {
                "COLUMN_NAME": "zone_name",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "High Profile Zone Name",
                "DISPLAY_ORDER": 6,
            },

            {
                "COLUMN_NAME": "is_submited",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Form Submitted",
                "DISPLAY_ORDER": 7,
            },
            ],
        }
    },
    updated: function () {
        debugger;
        //this.BindActionButtonEvent();
        this.SetScrolbar('dark-thick', '', 'filterscroll_leftsearch');
    },
    methods: {
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            //this.FetchRecords();
        },
        workorderActionButtonClick(callbackfunction, item, index, event) {
            debugger;
            switch (callbackfunction) {
                case 'WorkOrderStatus':
                    this.openPopupForUpdateStatus(item);
                    break;
                case 'WorkOrderDetails':
                    this.WordOrderDetailLink(item);
                    break;
            }
        },
        WordOrderDetailLink: function (item) {
            var vm = this;
            vm.$router.push({ path: "/Ticketing/WorkOrderDetail/" + item.id + "/" + item.ticket_id + "" });
        },
        openPopupForUpdateStatus: function (item) {
            var vm = this;
            vm.updateWorkOrderStatus = item;
            vm.showWorkOrderStatusModal = !vm.showWorkOrderStatusModal;
        },
        getStatusClassList: function (status) {
            debugger
            var returnClass = '';
            switch (status) {
                case 'UnPublish':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                case 'Active':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'Reopen':
                    returnClass = 'px-2 bg-primary text-center text-white d-block';
                    break;
                case 'On Hold':
                    returnClass = 'px-2 bg-warning text-center text-white d-block';
                    break;
                case 'Draft':
                    returnClass = 'px-2 text-center bg-warning text-white d-block';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center bg-danger text-white d-block';
                    break;
                case 'Open':
                    returnClass = 'px-2 text-center text-white d-block bg-success';
                    break;
                case 'Waiting':
                    returnClass = 'px-2 text-center bg-success text-white d-block';
                    break;
                case 'In progress':
                    returnClass = 'px-2 bg-secondary text-center text-white d-block';
                    break;
                case 'Completed':
                    returnClass = 'px-2 bg-secondary text-center text-white d-block';
                    break;
                case 'Cancelled':
                    returnClass = 'px-2 bg-danger text-center text-white d-block';
                    break;
                case 'Closed':
                    returnClass = 'px-2 bg-danger text-center text-white d-block';
                    break;
                case 'Closed and Locked':
                    returnClass = 'px-2 bg-danger text-center text-white d-block';
                    break;
                case 'Waiting Resolved':
                    returnClass = 'px-2 bg-secondary text-center text-white d-block';
                    break;
                case 'Waiting Response':
                    returnClass = 'px-2 bg-secondary text-center text-white d-block';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        renderActions: function (row) {
            debugger;
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Detail':
                        action.isVisible = self.IsInSlot == true && (row.TICKET_AR_TYPE == '' && row.IS_SHOW_ACCEPT_REJECT == 0);
                        break;
                    case 'Update Status':
                        action.isVisible = self.IsInSlot == true && (row.TICKET_AR_TYPE == '' && row.IS_SHOW_ACCEPT_REJECT == 0);
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
    }
}
</script>