          else if (vm.mapType == "MapBox") {
            setTimeout(() => {
               vm.LoadMapboxHeatMap(lat,lng);
            }, 4000);
          }
          else {
            setTimeout(() => {
              vm.ShowEtaMap(lat,lng);
            }, 4000);
          }
