      initMapBox: function (obj, apikey, vueObj, indexMap) {
        var vm = this;
        var center = "";
        let zoom = 3;
        if (obj) {
          var item = $.grep(obj, function (element, index) {
            return (
              element.location_nw != "" &&
              element.location_nw != null &&
              typeof element.location_nw != undefined &&
              element.location_nw != ","
            );
          });
          item = item[0];
          if (item) {
            center = [
              parseFloat(String(item.location_nw).split(",")[1], 10),
              parseFloat(String(item.location_nw).split(",")[0], 10),
            ];
          }
        }
        vm.startingPoint = center;
        mapboxgl.accessToken = apikey;
        vm.map = new mapboxgl.Map({
          container: vm.mapContainer,
          style: "mapbox://styles/mapbox/streets-v11",
          zoom: zoom,
          zoomControl: true,
          scrollZoom: true,
          boxZoom: true
        });
        if(vm.config!=undefined && vm.config.isShowSearchBox==true)
        {
          var geocoder = new MapboxGeocoder({
          // Initialize the geocoder
            accessToken: apikey, // Set the access token
            mapboxgl: mapboxgl, // Set the mapbox-gl instance
            marker: false, // Do not use the default marker style
            placeholder: 'Search for places in Barbados', // Placeholder text for the search bar
          });
          vm.map.addControl(geocoder);
        }
        
        // vm.map.scrollZoom.disable();
        if(vm.config!=undefined && vm.config.isShowDrawButton==true){
          var draw = new MapboxDraw({
          // Instead of showing all the draw tools, show only the line string and delete tools
                displayControlsDefault: false,
