                      line_string: false,
                      polygon: true,
                      point: true,
                      trash: true
                  }
                });
                // Add the draw tool to the map
                vm.map.addControl(draw);
                vm.map.on('draw.create', vm.GetMapBoxAddressToDraw);
                var nav = new mapboxgl.NavigationControl();
                vm.map.addControl(nav, 'top-left');
        }
        // Add zoom and rotation controls to the map.
        //map.addControl(new mapboxgl.NavigationControl());
        var nav = new mapboxgl.NavigationControl();
        vm.map.addControl(nav, "bottom-right");
        if (obj != null && obj.length > 0) {
          $.each(obj, function (key, value) {
              key++;
              //if(vm.Data.length==1){
                vm.map.flyTo({
                  center: [vm.Data[0].latlong.split(',')[1],vm.Data[0].latlong.split(',')[0]],
                  zoom:4,
                  essential: true // this animation is considered essential with respect to prefers-reduced-motion
                });
              //}
              //let loc = '<span style="font-weight:bold;color:#303030;font-size:1pt;">' + value.Formated_address + ' ' + value.State + '</span><br/><span style="color:#555;font-size:10pt;line-height:22px;">' + value.TypeofWork + '</span><span style="color:#007bff;font-size:10pt;line-height:22px;"><br/>' + value.TicketNumber + '</span>';
              var fileCount = 0;
              var videoCount = 0;
              var imageCount = 0;
              if (value.attachmentCountWithExt != "[]" && value.attachmentCountWithExt != "" && value.attachmentCountWithExt != null
              ) {
                var countArray = JSON.parse(value.attachmentCountWithExt);
                $.each(countArray, function (index, AttItem) {
                  if (AttItem.TYPE == "Video") {
                    videoCount = AttItem.COUNT;
                  } else if (AttItem.TYPE == "Image") {
                    imageCount = AttItem.COUNT;
                  } else {
                    fileCount += parseInt(AttItem.COUNT);
                  }
                });
              }
              if(value.polygons[key]!=undefined){
                    var campus = {
                    "type": "Feature",
                    "properties": {
                        "popupContent": 'dfsf',
                        "style": {
                            weight: 2,
                            color: "#FF0000",
                            opacity: 0.5,
                            fillColor: "#FF0000",
                            fillOpacity: 0.2
                        }
                    },
                    "geometry": {
                        "type": "MultiPolygon",
                        "coordinates": [
                            [
                                [
                                  [value.polygons[key].latLong[key].latitude.split(',')[1],value.polygons[key].latLong[key].latitude.split(',')[0]]
                                ]
                            ]
                        ]
                      }
                };
              vm.map.addSource('maine', {
                  type: 'geojson',
                  data: geoJSON
              });
              vm.map.addLayer({
                  'id': 'maine',
                  'type': 'fill',
                  'source': 'maine',
                  'layout': {},
                  'paint': {
                      'fill-color': '#FF0000',
                      'fill-opacity': 0.35
                  }
              });
              }
              let popup = '<div class="gm-style-iw gm-style-iw-c">' +
                          '<div class="gm-style-iw-d"><div><div id="content">'+
                          '<div class="map-pop-header ' + value.priority + '" style="background-color:' + value.color_code + '">'+
                          '<div id="siteNotice"></div>'+ value.popupHeaderHtml+ '</div>' +
                          '<div id="bodyContent">'+value.popupBodyHtml+
                          '<p class="view-details">'+value.popupFooterHtml + '</p></div></div></div></div></div>';
              
              //getCoordinates(loc, value.priority, key, value.location_nw, value.TicketNumber);
              GetClusterData(key,value.pinLatLong.latitude,obj,value.priority,popup,value.is_high_profile,value);
            });
          if (obj.mapdetail != null && obj.mapdetail.length > 0) {
            if (obj.mapdetail[0].MAP_TYPE == "MapBox") {
              if (obj.mapdetail[0].LONGITUDE != "" && obj.mapdetail[0].LATITUDE != "" && obj.mapdetail[0].LAT_LNG != "") {
                var bound = JSON.parse(obj.mapdetail[0].LAT_LNG);
                var bounds = bound.reduce(function (bounds, coord) {
                  return bounds.extend(coord);
                }, new mapboxgl.LngLatBounds(bound[0], bound[0]));
                map.fitBounds(bounds);
              }
            } else {
              if (
                obj.mapdetail[0].LONGITUDE != "" &&
                obj.mapdetail[0].LATITUDE != "" &&
                obj.mapdetail[0].LAT_LNG != ""
              ) {
                var googleJson = [];
                $.each(
                  JSON.parse(obj.mapdetail[0].LAT_LNG),
                  function (key, item) {
                    var dummy = [];
                    if (
                      obj.mapdetail[0].MAP_TYPE == "LeafLet" ||
                      obj.mapdetail[0].MAP_TYPE == "Esri"
                    )
                      dummy.push(item[1], item[0]);
                    else {
                      dummy.push(item.lng, item.lat);
                    }
                    googleJson.push(dummy);
                  }
                );
                var bound = googleJson;
                var bounds = bound.reduce(function (bounds, coord) {
                  return bounds.extend(coord);
                }, new mapboxgl.LngLatBounds(bound[0], bound[0]));
                map.fitBounds(bounds);
              }
            }
          }
          $(".mapboxgl-ctrl-compass").hide();
          var geoJSON;
  
          function GetClusterData(key,location,objData,priority,address,isHighProfile,item) {
            if (location != "" && location != null && typeof location != "undefined" && typeof location != ",") {
              var lat = parseFloat(String(location.split(",")[0]), 10);
              var lng = parseFloat(String(location.split(",")[1]), 10);
              var mapicon = "https://app.talygen.com";
              var color = item.color_code;
              if (
                color == "" ||
                color == null ||
                typeof color == "undefined" ||
                typeof color == " "
              ) {
                color = "#4db567";
              }
              if (isHighProfile == "yes") {
                mapicon +=
                  "https://talygenv.github.io/Vue-UI-Content/images/Ticketing/mapicon_critical.png";
              } else if (priority == "Critical") {
                mapicon +=
                  "https://talygenv.github.io/Vue-UI-Content/images/Ticketing/mapicon_critical.png";
              } else if (priority == "High") {
                mapicon +=
                  "https://talygenv.github.io/Vue-UI-Content/images/Ticketing/mapicon_critical.png";
              } else if (priority == "Medium") {
                mapicon +=
                  "https://talygenv.github.io/Vue-UI-Content/images/Ticketing/mapicon_critical.png";
              } else {
                mapicon +=
                  "https://talygenv.github.io/Vue-UI-Content/images/Ticketing/mapicon_critical.png";
              }
              if (key == 1 || typeof geoJSON == "undefined") {
                geoJSON = {
                  type: "FeatureCollection",
                  features: [
                    {
                      type: "Feature",
                      properties: {
                        title: "Ticket Priority: " + priority,
                        icon: "" + mapicon,
                        color: color,
                        address: address,
                        ticketNumber:item.refValue
                      },
                      geometry: {
                        type: "Point",
                        coordinates: [],
                      },
                    },
                  ],
                };
                geoJSON.features[0].geometry.coordinates.push(lng);
                geoJSON.features[0].geometry.coordinates.push(lat);
                vm.markers.push(geoJSON);
              } else {
                var obj = {
                  type: "Feature",
                  properties: {
                    title: "Ticket Priority: " + priority,
                    icon: "" + mapicon,
                    color: color,
                    address: address,
                    ticketNumber:item.refValue
                  },
                  geometry: {
                    type: "Point",
                    coordinates: [],
                  },
                };
                obj.geometry.coordinates.push(lng);
                obj.geometry.coordinates.push(lat);
                geoJSON.features.push(obj);
                vm.markers.push(obj);
              }
            }
            if (key == objData.length) {
              vm.map.loadImage("https://talygenv.github.io/Vue-UI-Content/images/Ticketing/mapicon_critical.png",(error, image) => {
                  if (error) {
                    throw error;
                  }
                  vm.map.addImage("ticket-icon", image, { sdf: true });
                }
              );
              vm.map.on("load", function () {
                vm.map.addSource("ticketsourcedata", {
                  type: "geojson",
                  data: geoJSON,
                  cluster: true,
                  clusterMaxZoom: 14, // Max zoom to cluster points on
                  clusterRadius: 50, // Radius of each cluster when clustering points (defaults to 50)
                });
                vm.map.addLayer({
                  id: "clusters",
                  type: "circle",
                  source: "ticketsourcedata",
                  filter: ["has", "point_count"],
                  paint: {
                    "circle-color": [
                      "step",
                      ["get", "point_count"],
                      "#51bbd6",
                      100,
                      "#f1f075",
                      750,
                      "#f28cb1",
                    ],
                    "circle-radius": [
                      "step",
                      ["get", "point_count"],
                      20,
                      100,
                      30,
                      750,
                      40,
                    ],
                  },
                });
                vm.map.addLayer({
                  id: "cluster-count",
                  type: "symbol",
                  source: "ticketsourcedata",
                  filter: ["has", "point_count"],
                  layout: {
                    "text-field": "{point_count_abbreviated}",
                    "text-font": ["DIN Offc Pro Medium", "Arial Unicode MS Bold"],
                    "text-size": 12,
                  },
                });
                vm.map.addLayer({
                  id: "unclustered_point",
                  type: "circle",
                  source: "ticketsourcedata",
                  filter: ["!", ["has", "point_count"]],
                  paint: {
                    "circle-color": ["get", "color"],
                    "circle-radius": 20,
                    "circle-stroke-width": 1,
                    "circle-stroke-color": "#fff",
                  },
                });
                // inspect a cluster on click
                vm.map.on("click", "clusters", function (e) {
                  var features = vm.map.queryRenderedFeatures(e.point, {
                    layers: ["clusters"],
                  });
                  var clusterId = features[0].properties.cluster_id;
                  vm.map
                    .getSource("ticketsourcedata")
                    .getClusterExpansionZoom(clusterId, function (err, zoom) {
                      if (err) return;
                      vm.map.easeTo({
                        center: features[0].geometry.coordinates,
                        zoom: zoom,
                      });
                    });
                });
                // inspect a cluster on click              
                vm.map.on("click", "unclustered_point", function (e) {
                  if(objData[0].popupBodyHtml!=""){
                    if (e.features[0].properties.cluster != true) {
                      var coordinates = e.features[0].geometry.coordinates.slice();
                      var description = e.features[0].properties.address;
                      new mapboxgl.Popup({closeOnClick: true})
                        .setLngLat(coordinates)
                        .setHTML(description)
                        .addTo(vm.map);
                    }
                  }
                 
                });
                vm.map.on("mouseenter", "clusters", function () {
                  vm.map.getCanvas().style.cursor = "pointer";
                });
                vm.map.on("mouseleave", "clusters", function () {
                  vm.map.getCanvas().style.cursor = "";
                });
                vm.map.on("mouseenter", "unclustered_point", function () {
                  vm.map.getCanvas().style.cursor = "pointer";
                });
                vm.map.on("mouseleave", "unclustered_point", function () {
                  vm.map.getCanvas().style.cursor = "";
                });
                if (vm.map) {
                  vm.map.on("wheel", (event) => {
                    if (event.originalEvent.ctrlKey) {
                      // Check if CTRL key is pressed
                      event.originalEvent.preventDefault(); // Prevent chrome/firefox default behavior
                      if (!vm.map.scrollZoom._enabled) vm.map.scrollZoom.enable(); // Enable zoom only if it's disabled
                    } else {
                      if (vm.map.scrollZoom._enabled) vm.map.scrollZoom.disable(); // Disable zoom only if it's enabled
                    }
                  });
                }
              });
            }
          }
        }
      },
      initLeafletMap: function (obj, vueObj, indexMap) {
        var vm=this;
