        });
        if(vm.Data.length==1 && vm.Data[0].latlong!=undefined){
          vm.map.flyTo([vm.Data[0].latlong.split(',')[0],vm.Data[0].latlong.split(',')[1]], 16, {
            animate: true,
            duration: 2 // in seconds
          });
