        var CriticalIcon = new LeafIcon({
          iconUrls: "https://talygenv.github.io/Vue-UI-Content/images/Ticketing/mapicon_critical.png",
        });
        var HighIcon = new LeafIcon({
          iconUrl: "https://talygenv.github.io/Vue-UI-Content/images/Ticketing/mapicon_high.png",
        });
        var LowIcon = new LeafIcon({
          iconUrls: "https://talygenv.github.io/Vue-UI-Content/images/Ticketing/mapicon_low.png",
        });
        var MediumIcon = new LeafIcon({
          iconUrls: "https://talygenv.github.io/Vue-UI-Content/images/Ticketing/mapicon_medium.png",
        });
        var fillIcon = new LeafIcon({
          iconUrls: "https://talygenv.github.io/Vue-UI-Content/images/Ticketing/mapicon_fill.png",
        });
        if (obj != null && obj.length > 0) {
          SetCluster(obj);
        }
