      ShowEtaMap:function(lats,lngitude){
          var vm=this;
          var lng = null;
          var lat = null;
          var profile = 'driving-car';
          var minutes = 10 * 60;//in seconds
          var range = 30 * 60;
          if(vm.showHideEtaMap){
            $.each(vm.mapLayer, function (index,layer) {
              vm.map.removeLayer(layer);
            });
            vm.showHideEtaMap=false;
            return false;
          }
          getLocation();
          function getLocation() {
              if (navigator.geolocation) {
                  navigator.geolocation.getCurrentPosition(savePosition, positionError, {timeout:10000});
              } else {
                  alert('Geolocation is not supported by this browser');
              }
          }
          function positionError(error) {
              var errorCode = error.code;
              var message = error.message;
  
              alert(message);
          }
          var position='';
          function savePosition(position) {
               position=$.post("geocoordinates.php", {lat: position.coords.latitude, lng: position.coords.longitude});
          }
          lng = parseFloat(lngitude, 10) + 0.02;
          lat = parseFloat(lats, 10) + 0.004;
          initMap(lat, lng);
          function initMap(lat, lng) {
              if (vm.map) {
                $.each(vm.mapLayer, function (index,layer) {
                  vm.map.removeLayer(layer);
                });
                vm.showHideEtaMap=true;
                  var latLng = new L.latLng(lat, lng);
                  vm.map.panTo(latLng);
                  vm.map.setZoom(11);
              }
              var mapicon = localStorage.getItem("UserImage");
              if (String(mapicon).indexOf('blob') <= 0) {
                  mapicon = 'https://app.talygen.com/Content/images/Black/user.png'
              }
              var markerIcon = L.icon({
                  iconUrl: mapicon,
                  iconSize: [40, 40], // size of the icon
              });
            
              var popup = L.popup().setContent("<div style='padding:8px;'><img style='float:left;' src='" + mapicon + "' class='marker' /> <div style='display:block;'>"+vm.UserName+"</br>"+vm.UserEmailId+"</div></div>");
              var marker = L.marker([lat, lng], { icon: markerIcon },{layers:''}).bindPopup(popup).openPopup().addTo(vm.map);
              vm.imageMarker.push(marker);
              GetIsoData(true);
            }
          function GetIsoData(isLoad) {
              var rangeArray = [];
              $("input[name=duration]:checked").each(function () {
                  rangeArray.push($(this).val());
              });
              $.ajax({
                  type: "POST", //rest Type
                  dataType: 'json',
                  headers: { 'Authorization': '5b3ce3597851110001cf62485ecdd7b54dd545c4901722c53ac3d1d3' },
                  url: "https://api.openrouteservice.org/v2/isochrones/" + profile,
                  data: JSON.stringify({ location_type: "start", range_type: 'time', range: rangeArray, interval: minutes, locations: [[lng, lat], [lng, lat]], intersections:false}),
                  
                  async: false,
                  contentType: "application/json; charset=utf-8",
                  success: function (data) {
                      L.geoJson(data, {
                          onEachFeature: function (feature, layer) {
                              
                              layer.bindPopup("Isochrone: " + feature.properties.value);
                              vm.mapLayer.push(layer);
                          },
                          style: function (feature) {
                              var ratio = feature.properties.value;
                              vm.SetColor(ratio);
                              return {
                                  fillColor: vm.colors,
                                  color: vm.colors,
                                  weight: 1,
                                  fillOpacity: 0.35
                              };
                          }
                      }).addTo(vm.map);
                     
                  }
              });
          }
          document.getElementById("params").addEventListener('change', function (e) {
              if (e.target.name === 'profile') {
                  profile = e.target.value;
                  initMap(lat, lng);
              } else if (e.target.name === 'duration') {
                  var totalMinutes = "";
                  if ($(params).find("input:checkbox:checked").length > 0) {
                      $(params).find("input:checkbox:checked").each(function () {
                          if (totalMinutes.length > 0) {
                              totalMinutes += ","
                          }
                          var val = $(this).val();
                          totalMinutes += val;
                      });
                      var splitMin = String(totalMinutes).split(',');
                      minutes = splitMin[0];
                      range = splitMin[splitMin.length - 1];
                  }
                  else {
                      minutes = "600";
                      range = "1800";
                  }
                  initMap(lat, lng);
              }
          });
          // GetIsoData(true);
      },
      SetColor:function (duration) {
        var vm=this;
          duration = parseInt(duration, 10);
          if (vm.mapType != "MapBox") {
              duration = duration / 60;
          }
          else{
                vm.time=10;
          }
          if (vm.mapType == "MapBox") {
              switch (duration) {
                  case 10:
                      vm.colors = '3CEA19';
                      break;
                  case 20:
                      vm.colors = 'EFF60B';
                      break;
                  case 30:
                      vm.colors = 'F6320B';
                      break;
                  default:
                      vm.colors = 'F6320B';
              }
          }
          else {
              switch (duration) {
                  case 10:
                      vm.colors = 'green';
                      break;
                  case 20:
                      vm.colors = 'yellow';
                      break;
                  case 30:
                      vm.colors = 'red';
                      break;
                  default:
                      vm.colors = 'red';
              }
          }
      },
      LoadGoogleHeatMap:function(latitude,longitude) {
