              if (vm.map) {
                 $.each(vm.map, function (index,layer) {
                      try {
                        vm.map.removeLayer(layer);
                      }
                      catch (ex)
                      { }
                  });
                  vm.showHideEtaMap=true;
                  var latLng = new L.latLng(lat, lng);
                  vm.map.panTo(latLng);
                  vm.map.setZoom(11);
              }
