                  data: JSON.stringify({ location_type: "start", range_type: 'time', range: rangeArray, interval: minutes, locations: [[lng, lat], [lng, lat]], intersections: false }),
                  //url: "https://api.openrouteservice.org/isochrones?locations=" + lng + "," + lat + "&profile=" + profile + "&range_type=time&interval=" + minutes + "&range=" + range + "&units=&location_type=start&intersections=false&api_key=58d904a497c67e00015b45fc90fa91f0d345426145bb09e67e859771",
                  async: false,
                  contentType: "application/json; charset=utf-8",
                  success: function (data) {
                      vm.map.data.addGeoJson(data);
                  }
              });
              vm.map.data.setStyle(function (feature) {
                  var ratio = feature.getProperty('value');
