                          if (totalMinutes.length > 0) {
                              totalMinutes += ","
                          }
                          var val = $(this).val();
                          totalMinutes += val;
                      });
                      var splitMin = String(totalMinutes).split(',');
                      minutes = splitMin[0];
                      range = splitMin[splitMin.length - 1];
                  }
                  else {
                      minutes = "600";
                      range = "1800";
                  }
                  initMap(latitude, longitude);
              }
          });
