                            else if(item.types[0]=='administrative_area_level_2')
                            {
                                address.AdministrativeAreaLevel2.LongName=item.long_name;
                                address.AdministrativeAreaLevel2.Name=item.short_name;
                            }
                            else if(item.types[0]=='administrative_area_level_3')
                            {
                                vm.address.push({
                                  AdministrativeArea3:item.long_name
                                })
                                address.AdministrativeAreaLevel3.LongName=item.long_name;
                                address.AdministrativeAreaLevel3.Name=item.short_name;
                            }
                            else if(item.types[0]=='locality'){
                                vm.address.push({
                                  City:item.long_name
                                })
                                address.City.LongName=item.long_name;
                                address.City.Name=item.short_name;
                            }
                          }) 
                          vm.fullAddress.push({'LatLng':vm.lngLatData,'CompleteAddress':address})
                          vm.$emit('GetAddress',vm.fullAddress);   
                          }
                      });
              }
          //});
      },
      LoadMapboxHeatMap:function (lats,lngitude) {
        var vm=this;
        var latLng = null;
        if (typeof (vm.map.getLayer('isoLayer')) != 'undefined') {
                vm.map.removeLayer('isoLayer');
        }
        if (typeof (vm.map.getSource('iso')) != 'undefined') {
            vm.map.removeSource('iso');
            return false;
        }
          var params = document.getElementById('params');
          // Create variables to use in getIso()
          var urlBase = 'https://api.mapbox.com/isochrone/v1/mapbox/';
          var lati = null;
          var lngi = null;
          // Create variables to use in getIso()
          var urlBase = 'https://api.mapbox.com/isochrone/v1/mapbox/';
          var lon = parseFloat(lngi, 10);// + 0.02;
          var lat = parseFloat(lati, 10);// + 0.004;
          //lat = 43.133971;
          //lon = -76.088009;
          var profile = 'driving';
          var minutes = '10,20,30';
          vm.colors = '3CEA19,EFF60B,F6320B';
          LoadMapboxLayer();
          // Create a function that sets up the Isochrone API query then makes an Ajax call
          function getIso() {
              var query =
                urlBase +
                profile +
                '/' +
                lngitude +
                ',' +
                lats +
                '?contours_minutes=' +
                minutes + '&contours_colors=' + vm.colors +
                '&polygons=true&access_token=' +
                mapboxgl.accessToken;
              $.ajax({
                  method: 'GET',
                  url: query
              }).done(function (data) {
                  // Set the 'iso' source's data to what's returned by the API query
                  vm.map.getSource('iso').setData(data);
              });
          }
          vm.map.flyTo({
            center: [lngitude,lats],
            zoom:10,
            essential: true // this animation is considered essential with respect to prefers-reduced-motion
          });
            var Draw = new MapboxDraw();
            vm.map.addControl(Draw, 'bottom-left');
          // When a user changes the value of profile or duration by clicking a button, change the parameter's value and make the API query again
          params.addEventListener('change', function (e) {
              if (e.target.name === 'profile') {
                  profile = e.target.value;
                  if(profile=='foot-walking')
                  {
                    profile='walking'
                  }
                  else if(profile=='cycling-regular')
                  {
                    profile='cycling'
                  }
                  else if(profile=='driving-car')
                  {
                    profile='driving'
                  }
                  getIso();
              } else if (e.target.name === 'duration') {
                  var totalMinutes = "";
                  var colorName = "";
                  if ($(params).find("input:checkbox:checked").length > 0) {
                      $(params).find("input:checkbox:checked").each(function () {
                          if (totalMinutes.length > 0) {
                              totalMinutes += ","
                          }
                          if (colorName.length > 0) {
                              colorName += ","
                          }
                          var val = $(this).val();
                          totalMinutes += val;
                          vm.SetColor(val);
                          colorName += vm.colors;
                      });
                      minutes = totalMinutes;
                      vm.colors = colorName;
                      getIso();
                  }
                  else {
                      minutes = "10,20,30";
                      vm.colors = '3CEA19,EFF60B,F6320B';
                      getIso();
                  }
              }
          });
          vm.map.on('load', function () {LoadMapboxLayer();});
          function LoadMapboxLayer() {
              // When the map loads, add the source and layer
              vm.map.addSource('iso', {
                  type: 'geojson',
                  data: {
                      'type': 'FeatureCollection',
                      'features': []
                  }
              });
              vm.map.addLayer(
                {
                    'id': 'isoLayer',
                    'type': 'fill',
                    'source': 'iso',
                    'layout': {},
                    'paint': {
                        'fill-color': ['get', 'fillColor'],
                        'fill-opacity': 0.5
                    }
                },
                'poi-label'
              );
              var lngLat = {
                  lon: lngitude,
                  lat: lats
              };
              var mapicon = localStorage.getItem("UserImage");
              if (String(mapicon).indexOf('blob') <= 0) {
                  mapicon = 'https://app.talygen.com/Content/images/Black/user.png'
              }
              var popup = new mapboxgl.Popup({ offset: 25 }).setHTML("<div style='padding:8px;'><img style='float:left;' src='" + mapicon + "' class='marker' /> <div style='display:block;'>@loggedUserInfo.Name </br>@loggedUserInfo.Email</div></div>");
              var el = document.createElement('div');
              el.className = 'marker';
              el.style.backgroundImage = 'url(' + mapicon + ')';
              el.style.backgroundPosition = "top center";
              el.style.backgroundSize = "cover";
              el.style.width = '40px';
              el.style.height = '40px';
              // add marker to map
              var marker = new mapboxgl.Marker(el).setLngLat(lngLat).setPopup(popup).addTo(vm.map);
              vm.imageMarker.push(marker);
              // Make the API call
              getIso();
          }
      },
      TravelMode: function(tm){
        var vm=this;
        vm.time=vm.time-tm;
        if(vm.mapType=='LeafLet')
        {
          vm.ShowEtaMap();
        }
        else if(vm.mapType=='Google'){
          vm.LoadGoogleHeatMap();
        }
        else if(vm.mapType=='MapBox'){
          vm.LoadMapboxHeatMap();
        }
        
      },
      ShowTravelMode:function(){
        var vm=this;
        if(vm.showSettingPopup==false){
         $(".settingPopup").removeClass("d-none")
         vm.showSetting=false;
         vm.showSettingPopup=true;
        }
        else{
          $(".settingPopup").addClass("d-none")
          vm.showSetting=true;
          vm.showSettingPopup=false;
        }
      },
      GetMapBoxAddressToDraw:function(e) {
          var vm=this;
          let address={City:{Name:'',LongName:''},Country:{ Name:'',LongName:''},AdministrativeAreaLevel1:{Name:'',LongName:''},AdministrativeAreaLevel2:{Name:'',LongName:''},
                     AdministrativeAreaLevel3:{Name:'',LongName:''},PinCode:'',State:{Name:'',LongName:''},FullAddress:''};
          // const data = draw.getAll();
          if(e.features[0].geometry.type=='Polygon'){
              e.features[0].geometry.coordinates[0].forEach(function (item,i) {
                vm.lngLatData.push({
                  Lat:item[1],
                  Lng:item[0]
                })
              })
              var point = new google.maps.LatLng(e.features[0].geometry.coordinates[0][0][1], e.features[0].geometry.coordinates[0][0][0]);
              var geocoder = new google.maps.Geocoder();
                      geocoder.geocode({ 'latLng': point },
                          function (results, status) {
                            address.FullAddress=results[0].formatted_address;
                              if (status == google.maps.GeocoderStatus.OK) {
                                  results[0].address_components.forEach(function (item,i) {
                            //vm.address=[];
                            if(item.types[0]=='country'){
                             address.Country.Name=item.short_name;
                             address.Country.LongName=item.long_name;
                            }
                            else if(item.types[0]=='postal_code')
