<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full">
                        <h4 class="modal-title">{{ $t('Upload File') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                     <div class="modal-body">
                      <div class="col-md-12">
                        <div class="row">
                          <div class="col-md-12">
                            <div class="form-group">
                              <label for="Name">GeoJSONFile:<span class="text-danger">*</span></label>
                              <div class="custom-file">
                                <input type="file" name="FileSrc" id="flFiles" class="d-none"  v-validate="'required'" accept=".gpx" v-on:change="handleFileChange" />
                                <div class="bootstrap-filestyle input-group">
                                  <input type="text" class="form-control disabled" v-model="fileName" disabled="" autocomplete="off">
                                   <div class="input-group-append" style="cursor:pointer;">
                                    <span class="group-span-filestyle input-group-btn input-group-text bg-white" tabindex="0">
                                      <label title="Click here to select a file." for="flFiles" class="mb-0 font-weight-normal ">Choose File</label></span>
                                      <span class="input-group-text border-0 bg-transparent p-0 pl-2">
                                        <a href="javascript:;" class="round-icon-small btn-danger-light clsattclear" title="" data-toggle-tooltip="tooltip" data-original-title="Clear"><i class="fa fa-undo"></i></a>
                                        <a href="javascript:;" class="round-icon-small btn-danger-light clsattremove d-none" title="" data-toggle-tooltip="tooltip" data-original-title="Remove"><i class="fa fa-times"></i></a></span></div></div>
                              </div>
                            </div>
                          </div>
                        </div>
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"  v-on:OnSubmit="onSubmit"></dynamic-form>
                     </div>
                    </div>
                    <div class="modal-footer col-md-12">
                      <a v-on:click="SaveGeoJsonFile()" title="Import" name="btnSave" v-bind:class="{'disabled':(isSending === true)}" v-bind:disabled="isSending" tabindex="6" class="btn btn-success formbtn"><i class="fa fa-upload pr-2"></i>Import</a>
                      <a href="javascript:;" v-on:click="Close()" title="Cancel" class="btn btn-danger formbtn btnCancel"><i class="fa fa-close pr-2"></i>Cancel</a>
                    </div>
                </div>
            </div>
        </div>
    </div>
  </template>
  
  <script>
  export default {
    data(){
        return{
            trackPoints:[],
            latlng:[],
            polygonObj:{},
            fileName:'',
            FormSchema: [
                {
                    layoutType: "double",
                    Data: 
                    [
                      {
                        astype: "RadioField",
                        label: 'ZoneType',
                        name: "TypeFor",
                        value: "Locator",
                        placeholder: "",
                        config: {
                        options: [
                        {
                            name:'Locator Area',value:'Locator'
                        },
                        {
                            name:'High Profile',value:'High_Profile'
                        },
                        {
                            name:'Locator After Hours',value:'LocatorAfterHours'
                        },
                        {
                            name:'Yard',value:'Yard'
                        }
                    ],
                        showAddIcon: false,
                        onChange: this.onChangeRadio
                     },
                    validationRules: { "required": true},
                },
                {
                            astype: "TextField",
                            name: "Email Id",
                            value: "",
                            visibility:false,
                            placeholder: "Email Id",
                            chktxtIndex:0,
                            config: {
                             showAddIcon: true,
                             onAddButtonClick:this.AddEmailFeild,
                            },
                            validationRules: { "required": false, max: 100 },
                        }]
                }
            ],
        }
    },
    methods: {
      AddEmailFeild:function(){
            var vm=this;
            vm.FormSchema[0].Data.push({
                astype: "TextField",
                name: "txtZoneName",
                value: "",
                placeholder: "Email Id",
                chktxtIndex: (this.FormSchema)[0].Data.length,
                config: {
                        showAddIcon: true,
                        onaddIconBackground: 'bg-danger',
                        onAddButtonClick: this.RemoveFile,
                        addIcon: 'fa fa-trash'
                },
                validationRules: { "required": false, max: 100 },
            })
      },
      RemoveFile: function (e, field) {
            var vm = this;
            var checkindex = (vm.FormSchema)[0].Data.findIndex(x => x.chktxtIndex === field.chktxtIndex);
            var chechIndex1 = checkindex;
            if (checkindex > -1) {
                (vm.FormSchema)[0].Data.splice(checkindex, 1);
            }
             if (chechIndex1 > -1) {
                (vm.FormSchema)[0].Data.splice(chechIndex1, 1);
            }
      },
      onChangeRadio:function(e){
            if(this.FormSchema[0].Data[0].value=='Yard'){
                this.FormSchema[0].Data[1].visibility=true;
                this.FormSchema[0].Data[1].isVisible=true;
            }
            else{
                this.FormSchema[0].Data[1].visibility=false;
                this.FormSchema[0].Data[1].isVisible=false;
                if(this.zoneEditData.YARD_EMAILS.split(',').length>1){
                    this.zoneEditData.YARD_EMAILS.split(',').forEach((item,index)=>{
                        if(index!=0){
                            this.FormSchema[1].Data[index].visibility=false;
                        }
                    })
                }
            }
      },
      async handleFileChange(event) {
        var vm=this;
        const file = event.target.files[0];
        vm.fileName=event.target.files[0].name;
        if (!file) return;
  
        // Read the file
        const gpxData = await this.readGpxFile(file);
         vm.latlng.push({latLong:gpxData});
         vm.polygonObj = {
                    latlong:"",
                    refValue:  vm.zoneId,
                    popupHeaderHtml: '',
                    popupHeaderColor: "",
                    popupBodyHtml: '',
                    popupFooterHtml: '',
                    pinLatLong: {
                        latitude: "",
                        longitude: ""
                    },
                    polygons: vm.latlng,
                    address: "",
                    priority:'',
                    color_code:''
                }  
        // Pass the extracted points to parent component or handle as needed
        //this.$emit('gpxData', vm.polygonObj);
        
      },
      Close(){
        var vm=this;
        vm.$emit("Close");
      },
      SaveGeoJsonFile:function(){
        debugger
        var vm=this;
        var email='';
        if(vm.fileName==""){
          vm.ShowAlert("Please Select File","warning");
          return false;
        }
        var zoneType=vm.FormSchema[0].Data[0].value;
        if(vm.FormSchema[0].Data[0].value=='Yard'){
          vm.FormSchema[0].Data.forEach((item,index)=>{
              if(index!=0){
                  email+=item.value+','
              }
          })
        }
        vm.$emit('gpxData',vm.polygonObj,email,zoneType);
        vm.$emit("Close");
      },
      async readGpxFile(file) {
        return new Promise((resolve, reject) => {
          const reader = new FileReader();
  
          reader.onload = (event) => {
            const gpxString = event.target.result;
            const gpxData = this.parseGpx(gpxString);
            resolve(gpxData);
          };
  
          reader.onerror = (error) => {
            reject(error);
          };
  
          reader.readAsText(file);
        });
      },
      parseGpx(gpxString) {
        var vm=this;
        const parser = new DOMParser();
        const gpxXml = parser.parseFromString(gpxString, 'text/xml');
        vm.trackPoints=[];
        //const trackPoints = [];
        const trackSegments = gpxXml.querySelectorAll('trkpt');
        trackSegments.forEach((segment) => {
          const latitude = parseFloat(segment.getAttribute('lat'));
          const longitude = parseFloat(segment.getAttribute('lon'));
          //const ele = parseFloat(segment.querySelector('ele').textContent);
          const time = segment.querySelector('time').textContent;
          vm.trackPoints.push({ latitude, longitude, time });
        });
  
        return vm.trackPoints;
      },
    },
  };
  </script>
  