async function bindleftCommonSearchdropdown(instance, moduleName, filtername, data, AllCount) {
    var vm = instance;
    var param = "moduleName='" + moduleName + "'&type=" + filtername + ""
    var param1 = "moduleName=Ticketing&type=" + filtername + ""
    if (filtername == 'Priority') {
        vm.leftSearchFields[0].listOptions = [];
        vm.leftSearchFields[0].listOptions.push({
            name: 'All(' + AllCount + ')',
            value: ''
        });
        data.forEach(element => {
            let obj = {
                name: element.mapping_name + '(' + element.tab_count_data + ')',
                value: element.tab_mapping_id
            }
            vm.leftSearchFields[0].listOptions.push(obj);
        });
    }
    // else if(filtername=='FSM_TICKET_TYPE'){
    //     await DataServices.GetDDLData(param1).then((response) => {
    //         vm.leftSearchFields[4].listOptions=[];
    //         response.data.DATA.forEach(element => {
    //             let obj = {
    //                 name: element.name,
    //                 value: element.value
    //             }
    //             vm.leftSearchFields[4].listOptions.push(obj);
    //         });
    //     });
    // }
    else if (filtername == 'TICKET_RESOLVER') {
        await DataServices.GetDDLData(param1).then((response) => {
            if (response.data && response.data.DATA) {
                const targetField = vm.leftSearchFields.find(field => field.fieldName === 'Assigned To'); // Find the field by name
                if (targetField) {
                    targetField.listOptions = response.data.DATA.map(element => ({ name: element.name, value: element.value }));
                }
            } else {
                console.error('No data found in the response.');
            }
        });
    }
    // else if (filtername == 'Ticketing') {
    //     //debugger;
    //     var stausParams = "statusType=Ticketing"
    //     await DataServices.GetFsmStatusDDLData(stausParams).then((response) => {
    //         console.log("response",response);
    //         if (response.data  && response.data.length > 0) {
    //             const targetField = vm.leftSearchFields.find(field => field.fieldName === 'Ticket Status'); // Find the field by name
    //             if (targetField) {
    //                 targetField.listOptions = response.data.map(element => ({ name: element.STATUS, value: element.STATUS }));
    //             }
    //         }
    //         else {
    //             console.error('No data found in the response.');
    //         }

    //     });
    // }
    else if (filtername == 'FSM_TICKETING_STATUS') {
        await DataServices.GetDDLData(param1).then((response) => {
            if (response.data && response.data.DATA) {
                debugger;
                const targetField = vm.leftSearchFields.find(field => field.fieldName === 'Ticket Status'); // Find the field by name
                if (targetField) {
                    targetField.listOptions = response.data.DATA.map(element => ({ name: element.name, value: element.name }));
                }
            } else {
                console.error('No data found in the response.');
            }
        });
        
        // var stausParams = "statusType=Ticketing"
        // await DataServices.GetFsmStatusDDLData(stausParams).then((response) => {
        //     console.log("response",response);
        //     if (response.data  && response.data.length > 0) {
        //         const targetField = vm.leftSearchFields.find(field => field.fieldName === 'Ticket Status'); // Find the field by name
        //         if (targetField) {
        //             targetField.listOptions = response.data.map(element => ({ name: element.STATUS, value: element.STATUS }));
        //         }
        //     }
        //     else {
        //         console.error('No data found in the response.');
        //     }

        // });
    }
    else if (filtername == 'TICKET_SUPERVISOR') {
        await DataServices.GetDDLData(param1).then((response) => {
            if (response.data && response.data.hasOwnProperty('DATA')) {
                const allOption = { name: 'All', value: 'ALL' };
                const options = response.data.DATA.map(element => ({ name: element.name, value: element.value }));

                const targetField = vm.leftSearchFields.find(field => field.fieldName === 'Supervisor'); // Find the field by name
                if (targetField) {
                    targetField.listOptions = [allOption, ...options];
                }
            }
            else {
                console.error('No data found in the response.');
            }
        });
    }
    else if (filtername == 'Critical Approval') {
        const index = vm.leftSearchFields.findIndex(field => field.fieldName === 'Critical Approval');
        if (index !== -1) {
            // Create the objects
            let obj = {
                name: 'ALL',
                value: 'ALL'
            }
            let obj2 = {
                name: 'Approval Pending',
                value: 'APPROVE_PENDING'
            }
            let obj3 = {
                name: 'Approved',
                value: 'APPROVED'
            }
            // Push the objects to the listOptions array at the found index
            vm.leftSearchFields[index].listOptions = [obj, obj2, obj3];
        }
    }
    else if (filtername == 'Form Submit') {
        const index = vm.leftSearchFields.findIndex(field => field.fieldName === 'Form Submit');
        if (index !== -1) {
            // Create the objects
            let obj = {
                name: 'Yes',
                value: 1
            }
            let obj2 = {
                name: 'No',
                value: 0
            }
            // Push the objects to the listOptions array at the found index
            vm.leftSearchFields[index].listOptions = [obj, obj2];
        }

    }
    else if (filtername == 'TAG_DATA') {
        await DataServices.GetDDLData(param1).then((response) => {
            if (response.data && response.data.DATA) {
                const allOption = { name: 'All', value: 'ALL' };
                const options = response.data.DATA.map(element => ({ name: element.name, value: element.value }));

                const targetField = vm.leftSearchFields.find(field => field.fieldName === 'Tags'); // Find the field by name
                if (targetField) {
                    targetField.listOptions = [allOption, ...options];
                }
            }
            else {
                console.error('No data found in the response.');
            }

        });
    }
    else if (filtername == 'Pending Acceptance') {
        // debugger
        const index = vm.leftSearchFields.findIndex(field => field.fieldName === 'Pending Acceptance');
        if (index !== -1) {
            // Create the objects
            let obj = {
                name: 'All',
                value: 2
            }
            let obj2 = {
                name: 'Yes',
                value: 1
            }
            let obj3 = {
                name: 'No',
                value: 0
            }
            // Push the objects to the listOptions array at the found index
            vm.leftSearchFields[index].listOptions = [obj, obj2, obj3];
        }
    }

    else if (filtername == 'Recurring Type') {
        const index = vm.leftSearchFields.findIndex(field => field.fieldName === 'Recurring Type');
        if (index !== -1) {
            // Create the objects
            let obj = {
                name: vm.$t('ParentRecurringTicket'),
                value: 1
            }
            let obj2 = {
                name: vm.$t('RecurringTicket'),
                value: 2
            }
            // Push the objects to the listOptions array at the found index
            vm.leftSearchFields[index].listOptions = [obj, obj2];
        }
    }
    else if (filtername == 'FSM_USERS') {
        await DataServices.GetDDLData(param1).then((response) => {
            if (response.data && response.data.DATA) {
                const targetField = vm.leftSearchFields.find(field => field.fieldName === 'Open By'); // Find the field by name
                if (targetField) {
                    targetField.listOptions = response.data.DATA.map(element => ({ name: element.name, value: element.value }));
                }
            }
            else {
                console.error('No data found in the response.');
            }
        });
    }
    else if (filtername == 'DEPARTMENT') {
        await DataServices.GetDDLData(param1).then((response) => {
            if (response.data && response.data.DATA) {
                const targetField = vm.leftSearchFields.find(field => field.fieldName === 'Department'); // Find the field by name
                if (targetField) {
                    targetField.listOptions = response.data.DATA.map(element => ({ name: element.name, value: element.value }));
                }
            }
            else {
                console.error('No data found in the response.');
            }

        });
    }
    else if (filtername == 'TICKET_CATEGORY') {
        await DataServices.GetDDLData(param1).then((response) => {
            if (response.data && response.data.DATA) {
                const targetField = vm.leftSearchFields.find(field => field.fieldName === 'Category'); // Find the field by name
                if (targetField) {
                    targetField.listOptions = response.data.DATA.map(element => ({ name: element.name, value: element.value }));
                }
            }
            else {
                console.error('No data found in the response.');
            }
        });
    }
    else if (filtername == 'CLIENT') {
        await DataServices.GetDDLData(param1).then((response) => {
            if (response.data && response.data.DATA) {
                const targetField = vm.leftSearchFields.find(field => field.fieldName === 'Contract'); // Find the field by name
                if (targetField) {
                    targetField.listOptions = response.data.DATA.map(element => ({ name: element.name, value: element.value }));
                }
            }
            else {
                console.error('No data found in the response.');
            }

        });
    }
    else if (filtername == 'PRODUCT') {
        await DataServices.GetDDLData(param1).then((response) => {
            if (response.data && response.data.DATA) {
                const targetField = vm.leftSearchFields.find(field => field.fieldName === 'Product'); // Find the field by name
                if (targetField) {
                    targetField.listOptions = response.data.DATA.map(element => ({ name: element.name, value: element.value }));
                }
            }
            else {
                console.error('No data found in the response.');
            }

        });
    }

};

export default { buttons, leftsearchSchema, listActions, bindleftCommonSearchdropdown };