<template>
<div>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full">
                        <h4 class="modal-title">{{ $t('Manage Polygon Details') }}</h4>
                        <button type="button" class="close" v-on:click="$emit('Close')">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                    <div class="row">
                        <div class="col-lg-4">
                            <div class="form-group">
                                <label class="">Stroke Width</label>:{{StrokeWidthValue}}<span class="slidervalve" v-text="totalStrokeWidth"></span>
                                <input type="range" min="1" max="10" step ="1" class="cssSlider d-block mt-2 w-100" id="txtstrokeWidth" v-model="StrokeWidthValue"/>
                            </div>
                        </div>
                        <div class="col-lg-4">
                            <div class="form-group">
                                <label class="">StrokeOpacity</label>:{{ StrokeOpacityValue }}<span class="slidervalve" v-text="totalStrokeOpacity"></span>
                                <input type="range" min="0" max="1" step ="0.1" class="cssSlider d-block mt-2 w-100" id="txtStrokeOpacity" v-model="StrokeOpacityValue"/>
                            </div>
                        </div>
                        <div class="col-lg-4">
                            <div class="form-group">
                                <label class="">FillOpacity</label>:{{ FillOpacityValue }}<span class="slidervalve" v-text="totalFillOpacity"></span>
                                <input type="range" min="0" max="1" step ="0.1" class="cssSlider d-block mt-2 w-100" id="txtFillOpacity" v-model="FillOpacityValue"/>
                            </div>
                        </div>
                        <div class="col-lg-4">
                            <div class="form-group">
                                    <label class="">Stroke Color</label>:
                                    <input type="color" id="txtstrokeColor" :value="txtstrokeColor" title="Stroke Color" class="dynamiccolorbox cursor-pointer"/>
                                </div>
                            </div>

                            <div class="col-lg-4">
                                <div class="form-group">
                                    <label class="">Fill</label>:
                                    <input type="color" id="txtFillColor" :value="fillColor" title="Fill Color" class="dynamiccolorbox cursor-pointer"/>
                                </div>
                            </div>
                    </div>
                    <div id="accordion" class="col-12 pt-2 activity-tab p-0 float-left">
                        <div class="card mb-3">
                            <div class="card-header DefaultTab">
                                                    <a class="card-link" id="ancreplyExpandAllCollapseAll"  @click="ancreplyExpandAllCollapseAll()" data-toggle="collapse" href="#collapseone" aria-expanded="true">
                                                        <div class="conversation-date heading-border">
                                                            <span><i class="fa fa-angle-down rotate-icon pr-1" aria-hidden="true"></i>Locator</span>  
                                                           
                                                        </div>
                                                    </a>
                                                </div>
                    <div id="collapseone" class="collapse show" data-parent="#accordion" style="">
                        <div class="card-body">
                    <div class="row mt-0">
                        <div class="col-md-6 col-sm-12 float-left mb-2">
                            <div class="form-group">
                                <input type="search" placeholder="Search by name" v-model="Locator" class="form-control">
                            </div>
                        </div> 
                        <div class="col-md-6 col-sm-12 float-left">
                            <div class="background-0 text-left form-check-inline search-btm-btn">
                                <a title="" href="javascript:;" @click="GetUserSkillList" class="btn btn-success mr-1 px-4" data-toggle-tooltip="tooltip" data-original-title="Search"><i class="fa fa-search"></i></a> 
                                <a href="javascript:;" title="" @click="RefreshList" class="btn btn-danger clearsearchtext px-4" data-toggle-tooltip="tooltip" data-original-title="Clear all "><i class="fa fa-refresh"></i></a>
                            </div>
                        </div>
                    </div>
                    <tg-list :IsShowAction="false" ModuleName="Ticketing" SubModuleCode="" IdentityColumn="user_id"
                        :HeaderText="$t('Zone Management')" :LegendArray="[]" v-on:ActionButtonClick="actionButtonClick"
                        :RenderRowActionMethod="renderActions" :widgets="[]" :listType="['List']" :ListData="LocatorData" :HeaderData="Headers"
                        HeaderButtons="" :TotalRecords="TotalRecords"
                        :ListDataCallBackFunction="FetchData" :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy"
                        :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
                        v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
                        v-on:SortdataButtonClick="sortdata">
                    </tg-list>
                    </div>
                    </div>
                </div>
                    </div>
                    <div class="row flex-row-reverse pt-3">
                        <div class="col-lg-12 text-right">
                            <a title="" name="btnSave" tabindex="6" @click="SaveStyleShape" class="btn btn-success formbtn"><i class="fa fa-save pr-2"></i>Save Polygon</a>&nbsp;
                            <a title="" name="btnDelete" tabindex="6" @click="DeleteFeature" class="btn btn-danger formbtn"><i class="fa fa-trash pr-2"></i>Delete Polygon</a>
                        </div>
                    </div>
                    </div>
                    
                </div>
            </div>
        </div>
    </div>
</div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    created:function(){
        debugger
        var vm=this;
        vm.GetUserSkillList();
        vm.polygonData.forEach(function (item,i) {
            //debugger
            if(item.Id==vm.polygonNewShapeId){
                vm.FillOpacityValue=vm.polygonData[i].strokeOpacity;
                vm.StrokeOpacityValue=vm.polygonData[i].strokeOpacity;
                vm.totalStrokeWidth=vm.polygonData[i].strokeWeight;
                vm.txtstrokeColor=vm.polygonData[i].strokeColor;
                vm.fillColor=vm.polygonData[i].fillColor;
            }
        });
        var val = "";
        $('.chkItems:checkbox').not("[id^='chkAll']").each(function () {
            vm.val=0;
            vm.val=$(this).val();
                vm.zoneEditData[0].locatorids.split(',').forEach(function (itm) {
                    if(itm==vm.val){
                        $(this).attr('checked',true);
                    }
                })
            });
    },
    props:{
        polygonNewShapeId:{
            type:String
        },
        polygonData:{
            type:String
        },
        zoneEditData:{
            type:String
        }
    },
    data() {
        return {
            val:0,
            Locator:'',
             Headers: [{
                "COLUMN_NAME": "first_name",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Name",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "resourceTypeName",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "ResourceType",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "skills",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Skill",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "department",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Department",
                "DISPLAY_ORDER": 3
            }
            ],
            LocatorData:[],
            tempLocatorData:[],
            StrokeWidthValue: "2",
            StrokeOpacityValue: "0.0",
            FillOpacityValue: "0.5",
            Showtab: 0
        }
    },
    methods:{
        GetUserSkillList: function(){
            debugger
            var vm=this;
            var params=`search=${vm.Locator}`
            DataServices.GetUserSkillList(params).then((response) => {
                vm.LocatorData=response.data.result.data;               
                setTimeout(function () { vm.ResponsiveDataTable('tablelistingdata') }, 500);
            })
        },
        ancreplyExpandAllCollapseAll: async function () {
            var vm = this;
            if (vm.Showtab == 0) {
                vm.Showtab = 1;
                $("#tab-ticket-content").find("#accordion").find('.collapse').addClass('show');
            } else {
                vm.Showtab = 0;
                $("#tab-ticket-content").find("#accordion").find('.collapse').removeClass('show');
            }
        },
        actionButtonClick(callbackfunction, item, index, event) {
            debugger
            
        },
        renderActions: function (row) {
            
        },
        leftCommonSearch: function (value) {
            debugger;
           
        },
        commonHeaderButtonClick: function (event) {
           
        },
        sortdata: function (sortBy) {
           
        },
        RefreshList:function(){
            var vm=this;
            vm.Locator='';
            vm.GetUserSkillList();
        },
        DeleteFeature:function(){
            var vm=this;
            vm.$emit("DeleteFeature");
        },
        SaveStyleShape:function(){
            debugger
            var vm=this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            let obj={
                Id:vm.polygonNewShapeId,
                strokeOpacity:vm.StrokeOpacityValue,
                strokeWeight:vm.StrokeWidthValue,
                strokeColor:document.getElementById("txtstrokeColor").value,
                fillOpacity: document.getElementById("txtFillOpacity").value,
                fillColor:document.getElementById("txtFillColor").value,
                locatorids:val
            }
            vm.$emit("GetZoneManagementData",obj)
        }
    }
}
</script>
<style>

    .mail-box-pop.dialog {
        background: rgba(0, 0, 0, 0.5) !important;
    }

    .gm-style .gm-style-iw {
        padding: 10px !important;
    }

        .gm-style .gm-style-iw button {
            display: none !important;
        }
    .cssSlider {
        -webkit-appearance: none;
        width: 23%;
        height: 17px;
        background: #d3d3d3;
        outline: none;
        opacity: 0.7;
        -webkit-transition: .2s;
        transition: opacity .2s;
        position: relative !important;
    }

        .cssSlider:hover {
            opacity: 1;
        }

        .cssSlider::-webkit-slider-thumb {
            -webkit-appearance: none;
            width: 25px;
            height: 25px;
            background: #04AA6D;
            cursor: pointer;
        }
</style>