<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full">
                        <template v-if="Status == 'Draft'">
                            <h4 class="modal-title">{{ $t('Recurring View Details') }} - {{ $t('DraftStatus') }}</h4>
                        </template>
                        <!-- Check if the ticket status is not 'Draft' -->
                        <template v-else>
                            <h4 class="modal-title">{{ $t('Recurring View Details') }}</h4>
                        </template>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <div class="modal-body">
                            <form data-vv-scope="ManageRecurringTicketData">
                                <div class="accordion custom-accr" id="accordionExample">
                                    <div class="card mb-3 probx">
                                        <div class="card-body">    
                                                                                 
                                            <div class="row" v-for="(item, index) in recurringTicketViewDetail"
                                                :key="index">
                                                <div class="col-md-6 col-lg-4">
                                                    <div class="form-group">
                                                        <label><b>{{ $t('Ticket Number:') }}</b>
                                                            {{ item.TICKET_NUMBER }} </label>
                                                    </div>
                                                </div>
                                                <div class="col-md-6 col-lg-4">
                                                    <div class="form-group">
                                                        <label><b>{{ $t('Frequency:') }}</b> {{ item.FREQUENCY }}</label>
                                                    </div>
                                                </div>
                                                <div class="col-md-6 col-lg-4"
                                                    v-if="item.FREQUENCY == 'Monthly' || item.FREQUENCY == 'Quarterly' || item.FREQUENCY == 'halfYearly' || item.FREQUENCY == 'Annually'">
                                                    <div class="form-group">
                                                        <label><b>{{ $t('Date:') }}</b> {{  $options.filters.formatDate(item.DATE)  ?
                                                            $options.filters.formatDate(item.DATE) : "--" }}</label>

                                                    </div>
                                                </div>
                                                <!-- <div class="col-md-6 col-lg-4">
                                                    <div class="form-group">
                                                        <label><b>{{ $t('Time:') }}</b> {{ item.TIME != null ?
                                                            item.TIME : "--" }}</label>
                                                    </div>
                                                </div> -->
                                                <div class="col-md-6 col-lg-4"
                                                    v-if="item.FREQUENCY == 'Weekly' || item.FREQUENCY == 'BiWeekly'">
                                                    <div class="form-group">
                                                        <label><b>{{ $t('Week Day:') }}</b> <span
                                                                v-html="getWeekDays(item.WEEK_DAYS)"></span></label>
                                                    </div>
                                                </div>
                                                <div class="col-md-6 col-lg-4">
                                                    <div class="form-group">
                                                        <label><b>{{ $t('Number of Recurrences:') }}</b>
                                                            {{ item.NO_OF_RECURRANCE }}</label>
                                                    </div>
                                                </div>
                                                <div class="col-md-6 col-lg-4">
                                                    <div class="form-group">
                                                        <label><b>{{ $t('Crew Name:') }}</b> {{ item.CREW_NAME != null ?
                                                            item.CREW_NAME : "--" }}</label>
                                                    </div>
                                                </div>
                                                <div class="col-md-6 col-lg-4">
                                                    <div class="form-group">
                                                        <label><b>{{ $t('Resource Name:') }}</b> {{ item.RESOURCE_NAME != ""
                                                            ? item.RESOURCE_NAME : "--" }}</label>
                                                    </div>
                                                </div>
                                                <div class="col-md-6 col-lg-4">
                                                    <div class="form-group">
                                                        <label><b style="float:left;">{{ $t('Status:') }}</b>
                                                            <span style="float: left;"
                                                                v-bind:class="{ 'px-2 text-center text-white d-block': true, 'bg-success': item.STATUS_ID == 'Active', 'bg-danger': item.STATUS_ID == 'Stopped' }">
                                                                {{ item.STATUS_ID }}
                                                            </span>
                                                        </label>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>

                                <div class="row flex-row-reverse mt-2">
                                    <div class="col-lg-6 text-right">
                                        <span><a href="javascript:;" class="btn btn-success mr-2 btn-sm"
                                                data-toggle-tooltip="tooltip" v-on:click="editRecurringTicket()"
                                                data-original-title="Edit Recurring">{{ $t('Edit Recurring') }}</a></span>
                                        <span><a href="javascript:;" class="btn btn-danger btn-sm"
                                                data-toggle-tooltip="tooltip" v-if="isStatus != 1003"
                                                data-original-title="Stop Recurring" v-on:click="StopRecurringTicket()">{{
                                                    $t('Stop Recurring') }} </a></span>
                                    </div>
                                </div>
                                <tg-list :IsShowAction="true" ModuleName="Ticketing" SubModuleCode="Ticketing" :LegendArray="[]"
                                    :widgets="[]" IdentityColumn="id" :listType="['List']"
                                    :HeaderText="$t('Recurring Ticket List')" :ListData="recurringTicketList"
                                    :HeaderData="Headers" :HeaderButtons="listheaderbuttons"
                                    :RenderRowActionMethod="listActions" :NorecordfoundText="$t('NoRecordfound')">
                                    <template v-slot:slotdata="{ data }">
                                        <template v-if="data.column.COLUMN_NAME == 'status_id'">
                                            <td class="text-center single-action tourguide_a">
                                                <span v-bind:class="getStatusClass(data.row.status_id, 'status_id')">
                                                    {{ data.row[data.column.COLUMN_NAME] }}
                                                </span>
                                            </td>

                                        </template>

                                    </template>
                                </tg-list>
                                <showRecurringTicketEditModal v-if="showRecurringTicketEditModal"
                                    v-on:close="showRecurringTicketEditModal = false" :TicketId="recurrTicketId">
                                </showRecurringTicketEditModal>




                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataServices from "@/services/DataService";
import Schema from "@/views/Ticketing/TicketingSchema";
import showRecurringTicketEditModal from './../../components/Modal/AddRecurringEditViewModal.vue';
export default {
    components: {
        showRecurringTicketEditModal
    },
    props: {
        TicketId: {
            type: Number,
            required: true
        },
        Status:{
            type:String,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            TicketBindList: [],
            recurringTicketViewDetail: [],
            recurringTicketList: [],
            ticketId: 0,
            listheaderbuttons: null,
            listActions:null,
            showRecurringTicketEditModal: false,
            SortBy: '',
            SortExp: '',
            Headers: [],
            Headers: [{
                "COLUMN_NAME": "TICKET_NUMBER",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Ticket Number",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "TICKET_SUBJECT",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Ticket Subject",
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "PRIORITY_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Priority Name",
                "DISPLAY_ORDER ": 3,
            },
            {

                "COLUMN_NAME": "ASSIGNED_TO",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Assigned To",
                "DISPLAY_ORDER": 4,

            },
            {
                "COLUMN_NAME": "STATUS_ID",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 2,
            },],
            user_id:null,
            companyId:null,
            isStatus:''
        }
    },
    created: async function () {
        debugger;
        var vm = this;
        vm.ticketId = vm.TicketId;
        vm.openRecurringTicketQuickView();
        vm.user_id = this.GetUserInfo.ID
        vm.companyId = this.GetUserInfo.CompanyId;
    },
    methods: {
        getWeekDays(days) {
            if (days != null) {
                var week_day = [];
                var day = days.split(',');
                day.forEach((value) => {
                    switch (value) {
                        case "0":
                            day = "--";
                            break;
                        case "1":
                            day = "Sunday";
                            break;
                        case "2":
                            day = "Monday";
                            break;
                        case "3":
                            day = "Tuesday";
                            break;
                        case "4":
                            day = "Wednesday";
                            break;
                        case "5":
                            day = "Thursday";
                            break;
                        case "6":
                            day = "Friday";
                            break;
                        case "7":
                            day = "Saturday";
                    }
                    week_day.push(day);
                });
                return week_day.join(", ");
            }
            else {
                return "--";
            }

        },
        convertToTimeFormatHHMMSS(value) {
            const sec = parseInt(value, 10); // convert value to number if it's string
            let hours = Math.floor(sec / 3600); // get hours
            let minutes = Math.floor((sec - (hours * 3600)) / 60); // get minutes
            let seconds = sec - (hours * 3600) - (minutes * 60); //  get seconds
            // add 0 if value < 10
            if (hours < 10) { hours = "0" + hours; }
            if (minutes < 10) { minutes = "0" + minutes; }
            if (seconds < 10) { seconds = "0" + seconds; }
            return hours + ':' + minutes + ':' + seconds; // Return is HH : MM : SS
        },        
        openRecurringTicketQuickView: function () {
            var vueObj = this;
            vueObj.isLoading=true;
            var params = `ticketId=${vueObj.ticketId}`;
            DataServices.GetRecurringViewDetailAndListing(params).then((response) => {
                vueObj.recurringTicketList = [];
                vueObj.recurringTicketViewDetail = [];
                if (response.data != null && response.data != "") {
                    if (response.data.status == "Success") {
                        vueObj.recurringTicketViewDetail = response.data.result.VIEW_RECURRING_DETAIL;
                        vueObj.recurringTicketList = response.data.result.RECURRING_TICKET_LISTING;
                        vueObj.isStatus = vueObj.recurringTicketViewDetail[0].REC_STATUS_ID;
                        vueObj.isLoading=false;
                    }
                }
            }, function (response) {
            });
        },
        editRecurringTicket: function () {
          //  debugger;
            var vm = this;
            vm.recurrTicketId = this.$parent.RecurringTicketId;
            vm.showRecurringTicketEditModal = !vm.showRecurringTicketEditModal;
        },
        Close: function () {
            this.$parent.showRecurringViewModal = false;
        },
        StopRecurringTicket:function(){            
            var vm = this;
            vm.recurrTicketId = this.$parent.RecurringTicketId;
            vm.confirmR(vm.$t("ConfirmRecurrence"), true, (vm.$t("Delete") + "  " + vm.$t("Reccuring")), function () {
                var postJSON = {
                    ids:  vm.recurrTicketId,
                    companyId: vm.companyId,
                    userId: vm.user_id,
                    moduleName: "Ticketing",
                    refCode: "TALYGEN_TICKET_RECURRING_INFO"
                };
                var objectPostString = JSON.stringify(postJSON);
                DataServices.deleteActivityData(objectPostString).then(response => {
                    if (response.status == 200) {                        
                        vm.openRecurringTicketQuickView();
                    }
                });
            })
        }        
    }
}
</script>