<template>
    <div>
        <div class="my-popups">
            <!-- <loader :is-visible="isLoading"></loader> -->
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full">
                            <h4 class="modal-title">Add Zone</h4>
                            <button type="button" class="close" v-on:click="$emit('Close')">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>
                        
                         <div class="modal-body">
                            <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"  v-on:OnSubmit="onSubmit"></dynamic-form>
                        </div>
                     </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props:{
        zoneEditData:{
            required:false
        }
    },
    data() {
        return {
            isLoading: false,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: 'Zone Name',
                            name: "txtZoneName",
                            value: "",
                            placeholder: "Enter Name",
                            validationRules: { "required": true, max: 100 },
                        }
                    ],
                },
                {
                    layoutType: "double",
                    Data: 
                    [
                      {
                        astype: "RadioField",
                        label: 'ZoneType',
                        name: "TypeFor",
                        value: "Locator",
                        placeholder: "",
                        config: {
                        options: [
                        {
                            name:'Locator Area',value:'Locator'
                        },
                        {
                            name:'High Profile',value:'High_Profile'
                        },
                        {
                            name:'Locator After Hours',value:'LocatorAfterHours'
                        },
                        {
                            name:'Yard',value:'Yard'
                        }
                    ],
                        showAddIcon: false,
                        onChange: this.onChangeRadio
                     },
                    validationRules: { "required": true},
                },
                {
                            astype: "TextField",
                            name: "Email Id",
                            value: "",
                            visibility:false,
                            placeholder: "Email Id",
                            chktxtIndex:0,
                            config: {
                             showAddIcon: true,
                             onAddButtonClick:this.AddEmailFeild,
                            },
                            validationRules: { "required": false, max: 100 },
                        }]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Description'),
                            name: "Description",    
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 500 },          
                        },
                    ],
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    onClick: this.Save
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ],
            ListData: []
        }
    },
    created:function(){
        debugger
        var vm=this;
       if(vm.zoneEditData[0].Refrence!='View'){

            vm.FormSchema[0].Data[0].value=vm.zoneEditData[0].ZONENAME;
            vm.FormSchema[1].Data[0].value=vm.zoneEditData[0].ZONETYPE;
            vm.FormSchema[2].Data[0].value=vm.zoneEditData[0].ZONEDESCRIPTION;
            //vm.FormSchema[1].Data[0].value=vm.zoneEditData[0].ZONE_TYPE;
            //vm.FormSchema[1].Data[1].value=vm.zoneEditData.YARD_EMAILS
            vm.zoneEditData[0].YARDEMAILS.split(',').forEach((item,index)=>{
                if(item!=""){
                    vm.FormSchema[1].Data.push({
                    astype: "TextField",
                    name: "txtZoneName",
                    value: item,
                    placeholder: "Email Id",
                    chktxtIndex: index+1,
                    config: {
                            showAddIcon: true,
                            onaddIconBackground: 'bg-danger',
                            onAddButtonClick: vm.RemoveFile,
                            addIcon: 'fa fa-trash'
                    },
                    validationRules: { "required": false, max: 100 },
                })
                }
                
            })
        }
        else{
            vm.FormSchema[0].Data[0].value=vm.zoneEditData[0].FormName;
            vm.FormSchema[1].Data[0].value=vm.zoneEditData[0].ZoneType;
            vm.FormSchema[2].Data[0].value=vm.zoneEditData[0].Description;
            vm.zoneEditData[0].YardEmails.split(',').forEach((item,index)=>{
                if(item!=""){
                    vm.FormSchema[1].Data.push({
                    astype: "TextField",
                    name: "txtZoneName",
                    value: item,
                    placeholder: "Email Id",
                    chktxtIndex: index+1,
                    config: {
                            showAddIcon: true,
                            onaddIconBackground: 'bg-danger',
                            onAddButtonClick: vm.RemoveFile,
                            addIcon: 'fa fa-trash'
                    },
                    validationRules: { "required": false, max: 100 },
                })
                }
                
            })
        }
    },
    methods:{
        onChangeRadio:function(e){
            if(this.FormSchema[1].Data[0].value=='Yard'){
                this.FormSchema[1].Data[1].visibility=true;
                this.FormSchema[1].Data[1].isVisible=true;
            }
            else{
                this.FormSchema[1].Data[1].visibility=false;
                this.FormSchema[1].Data[1].isVisible=false;
                if(this.zoneEditData.YARD_EMAILS.split(',').length>1){
                    this.zoneEditData.YARD_EMAILS.split(',').forEach((item,index)=>{
                        if(index!=0){
                            this.FormSchema[1].Data[index].visibility=false;
                        }
                    })
                }
            }
        },
        Close:function(){
            var vm=this;
            vm.$emit("Close");
        },
        RemoveFile: function (e, field) {
            var vm = this;
            var checkindex = (vm.FormSchema)[1].Data.findIndex(x => x.chktxtIndex === field.chktxtIndex);
            var chechIndex1 = checkindex;
            if (checkindex > -1) {
                (vm.FormSchema)[1].Data.splice(checkindex, 1);
            }
             if (chechIndex1 > -1) {
                (vm.FormSchema)[1].Data.splice(chechIndex1, 1);
            }
        },
        AddEmailFeild:function(){
            debugger
            var vm=this;
            vm.FormSchema[1].Data.push({
                astype: "TextField",
                name: "txtZoneName",
                value: "",
                placeholder: "Email Id",
                chktxtIndex: (vm.FormSchema)[1].Data.length,
                // config: {
                //  showAddIcon: true,
                //  onAddButtonClick:this.AddEmailFeild,
                // },
                config: {
                        showAddIcon: true,
                        onaddIconBackground: 'bg-danger',
                        onAddButtonClick: vm.RemoveFile,
                        addIcon: 'fa fa-trash'
                },
                validationRules: { "required": false, max: 100 },
            })
        },
        Save:function() {
            debugger
            var vm = this;
            var email='';
            if(vm.FormSchema[1].Data[0].value=='Yard'){
                vm.FormSchema[1].Data.forEach((item,index)=>{
                    if(index!=0){
                        email+=item.value+','
                    }
                })
            }
            let obj={
                ZoneId:vm.zoneEditData==undefined?0:vm.zoneEditData.ZONE_ID,
                Name:vm.FormSchema[0].Data[0].value,
                ZoneType:vm.FormSchema[1].Data[0].value,
                Description:vm.FormSchema[2].Data[0].value,
                YardEmails:email
            }
            vm.$emit('SaveZoneData',obj);
        },
    }
}
</script>
   